import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';

const useStyles = makeStyles((theme) => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function PopUp(props) {
  const classes = useStyles();
    console.log(props);
  const closePopup = () => {
    props.closePopup()
  };
  
  if(props.entity == null)
  return (<></>)

  return (
    <div>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={props.open}
        onClose={closePopup}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={props.open}>
        <div className=" col-4">
        <div className="module mid paper" style={{background: `url("${props.entity.background}") center center / cover`,}}>
              {console.log("props.entity", props.entity)}
            <h2 className="pt-5">{props.entity.name}
              <br />
              {props.entity.price.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,') + '₫'} 
            </h2>
          </div>
        </div>
        </Fade>
      </Modal>
    </div>
  );
}
