import { Component } from "react";

class Sort extends Component {
    componentWillReceiveProps(nextProps) {
        console.log("next", nextProps);
    }
    onClickSort = (sortBy, sortValue) => {
        console.log(sortBy, '-111', sortValue);
        this.props.onSort(sortBy, sortValue);
    }
    render() {
        return (
            <div>
                <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6">
                    <div className="dropdown">
                        <button className="btn btn-primary dropdown-toggle" type="button" id="dropdownMenu1" data-toggle="dropdown" aria-haspopup="true" aria-expanded="true">
                            Sắp Xếp <span className="fa fa-caret-square-o-down ml-5" />
                        </button>
                        <ul className="dropdown-menu" aria-labelledby="dropdownMenu1">
                            <li onClick={() => this.onClickSort('price', 1)}>
                                <a role="button" className={(this.props.sortBy === 'price' && this.props.sortValue === 1) ?
                                    'sort_selected' : ''}>
                                    <span className="fa fa-sort-alpha-asc pr-5">
                                        Giá từ thấp đến cao
                                    </span>
                                </a>
                            </li>
                            <li onClick={() => this.onClickSort('price', -1)}>
                                <a role="button" className={(this.props.sortBy === 'price' && this.props.sortValue === -1) ?
                                    'sort_selected' : ''}>
                                    <span className="fa fa-sort-alpha-desc pr-5">
                                        Giá từ cao đến thấp
                                    </span>
                                </a>
                            </li>
                            <li role="separator" className="divider" />
                            <li onClick={() => this.onClickSort('accountnr', 1)}>
                                <a role="button" className={(this.props.sortBy === 'accountnr' && this.props.sortValue === 1) ?
                                    'sort_selected' : ''}> Số tài khoản tăng dần
                                </a>
                            </li>
                            <li onClick={() => this.onClickSort('accountnr', -1)}>
                                <a role="button" className={(this.props.sortBy === 'accountnr' && this.props.sortValue === -1) ?
                                    'sort_selected' : ''}> Số tài khoản giảm dần
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        );
    }

}

export default Sort;