import { Component, useState } from 'react';
import './App.css';
import Title from './Components/Title';
import Control from './Components/Control';
import PopUp from './Components/PopUp';

const prices =
{
  lv1: {
    from: null,
    to: null
  },
  lv2: {
    from: 0,
    to: 1000000
  },
  lv3: {
    from: 1000000,
    to: 10000000
  },
  lv4: {
    from: 10000000,
    to: 100000000
  },
  lv5: {
    from: 100000000,
    to: 9999999999999
  },
}

class App2 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      datas: [],
      title: '',
      des: '',
      keyword: '',
      open: false,
      sort: {
        by: 'price',
        value: 1
      },
    }
  }

  componentWillMount() {
    this.getData();
  }

  currencyFormat(num) {
    console.log("1212", num.replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,'));
    return num.replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,') + '₫';
  }

  clicked = () => {
    this.setState({ show: !this.state.show });
  }

  getData(from = null, to = null) {
    console.log("inside handleGetJson", from, '-wwww', to);
    fetch(`/react_upload/Files/data.json`)
      .then(response => {
        return response.json();
      })
      .then(res => {
        console.log("aaaw", '-', res);
        var datas = res.filter((e) => (from == null || e.price >= from) && (to == null || e.price <= to));
        localStorage.setItem("datas", JSON.stringify(datas));
        this.setState({
          datas: datas
        });
      })
      .catch(err => {
        console.log("err", err)
      });
    this.loadTitle(to);
    console.log("title", to);
  }
  loadTitle(to) {
    console.log("title222", to);
    var title = '';
    var des = '';
    if (to === prices.lv1.to) {
      title = "Tất cả các mức giá";
      des = "Xem thông tin của tất cả số bên dưới";
    }
    else if (to === prices.lv2.to) {
      title = `Giá từ ${this.currencyFormat(prices.lv2.from.toString())} - ${this.currencyFormat(prices.lv2.to.toString())}`;
      des = "Xem thông tin của tất cả số bên dưới";
    }
    else if (to === prices.lv3.to) {
      title = `Giá từ ${this.currencyFormat(prices.lv3.from.toString())} - ${this.currencyFormat(prices.lv3.to.toString())}`;
      des = "Xem thông tin của tất cả số bên dưới";
    }
    else if (to === prices.lv4.to) {
      title = `Giá từ ${this.currencyFormat(prices.lv4.from.toString())} - ${this.currencyFormat(prices.lv4.to.toString())}`;
      des = "Xem thông tin của tất cả số bên dưới";
    }
    else {
      title = `Trên  ${this.currencyFormat(prices.lv4.to.toString())}`;
      des = "Xem thông tin của tất cả số bên dưới";
    }
    console.log("Core - ", to);
    this.setState({
      title: title,
      des: des
    })
  }

  onSort = (sortBy, sortValue) => {
    this.setState({
      sortBy: sortBy,
      sortValue: sortValue
    });
  }

  onSearch = (keyword) => {
    this.setState({
      keyword: keyword
    });
    this.getAllData(keyword);
    console.log("keyword -", keyword);
  }
  getAllData(filter) {
    console.log("filter", filter);
    fetch(`./react_upload/Files/data.json`)
      .then(response => response.json())
      .then(res => {
        var datas = res.filter((data) => {
          return data.name.toLowerCase().indexOf(filter) !== -1;
        });
        localStorage.setItem("datas", JSON.stringify(datas));
        this.setState({
          datas: datas
        });
      });
  }
  closePopup = () => {
    this.setState({
      open: false,
    });
  }

  popUp = (obj) => {
    this.setState({
      open: true,
      entity: obj
    });
  }
  render() {
    var {
      sortBy,
      sortValue,
      keyword
    } = this.state;
    console.log("datas", this.state.datas);
    if (sortBy === 'price') {

      this.state.datas.sort((a, b) => {
        if (a.price > b.price) return sortValue;
        else if (a.price < b.price) return -sortValue;
        else return 0
      })
    }
    else if (sortBy === 'accountnr') {
      this.state.datas.sort((a, b) => {
        if (a.name > b.name) return sortValue;
        else if (a.name < b.name) return -sortValue;
        else return 0
      })
    }


    var elmArray = this.state.datas.sort(e => e.price).map((x, index) => {
      let background = '';
      //if (x.id != null) {
      if (x.price > prices.lv2.from && x.price <= prices.lv2.to) {
        background = "/react_upload/Files/images/h2.jpg"
      }
      else if (x.price > prices.lv3.from && x.price <= prices.lv3.to) {
        background = "/react_upload/Files/images/h3.png"
      }
      else if (x.price > prices.lv4.from && x.price <= prices.lv4.to) {
        background = "/react_upload/Files/images/h1.png"
      }
      else {
        background = "/react_upload/Files//images/h4.png"
      }

      const style = {
        background: `url("${background}") center center / cover`,
      };
      x.background = background;
      return (
        <div className=" col-4" key={index} onClick={() => this.popUp(x)}>
          <div className="module mid" style={style}>
            <h2 className="pt-5">{x.name}
              <br />
              {this.currencyFormat(x.price + "")}
            </h2>
          </div>
        </div>
      );
      //}
    });
    console.log(elmArray);
    return (
      <div>    
        <div>
            <PopUp open={this.state.open} closePopup={this.closePopup} entity={this.state.entity}/>
        </div>
        <div className="row">
          <div className="col-3 col-s-3 menu">
            <ul>
              <li>Mức giá</li>
              <li onClick={() => this.getData()}>Tất cả giá</li>
              <li onClick={() => this.getData(prices.lv2.from, prices.lv2.to)}>0 - 1.000.000</li>
              <li onClick={() => this.getData(prices.lv3.from, prices.lv3.to)}>1.000.000 - 10.000.000</li>
              <li onClick={() => this.getData(prices.lv4.from, prices.lv4.to)}>10.000.000 - 100.000.000</li>
              <li onClick={() => this.getData(prices.lv5.from, prices.lv5.to)}>Hơn 100.000.000</li>
            </ul>
          </div>
          {/* ///title */}
          <div className="col-6 col-s-9">
            <Control
              onSearch={this.onSearch}
              onSort={this.onSort}
              sortBy={sortBy}
              sortValue={sortValue}
            />
            <Title title={this.state.title} description={this.state.des} />
            <div className="row">
              {elmArray}
            </div>
          </div>
          <div className="col-3 col-s-12">
            <div className="aside">
              <h2>What?</h2>
              <p>Bạn có thể tham khảo các số tài khoản MB.</p>
              <h2>Where?</h2>
              <p>Chúng tôi sẽ tư vấn đến bạn nên chọn tài khoản nào.</p>
              <h2>How?</h2>
              <p>Tại chi nhánh Phú Yên.</p>
            </div>
          </div>
        </div>
        <div className="footer">
          <p>Liên hệ với chúng tôi. Đặng Thị Bích Nhân. Số điện thoại: 039.5807329.</p>
        </div>
      </div>
    );
  }
}

export default App2;
